

























import {
  Component, Emit, Prop, Vue,
} from 'vue-property-decorator';
import { IQuestionItem } from '@/types/interface';

@Component
export default class QuestionResultPanel extends Vue {
  @Prop() res!: IQuestionItem;

  get isRight() {
    return this.res.select && this.res.right
      && this.res.select.sort().join(',') === this.res.right.sort().join(',');
  }

  checkAns(ans: string) {
    const select = this.res.select || [];
    const right = this.res.right || [];

    if (right.indexOf(ans) < 0 && select.indexOf(ans) < 0) return '';
    if (right.indexOf(ans) >= 0) return 'right-checkmark';
    if (right.indexOf(ans) < 0 && select.indexOf(ans) >= 0) return 'wrong-checkmark';
    return 'wrong-checkmark';
  }

  @Emit()
  sendError() {
    return this.res;
  }
}
